.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0 auto;
  background-color: hsl(0, 0%, 87%);
}
header {
  flex-basis: 5vh;
  background-color: hsl(190, 10, 12);
}
main {
  flex-grow: 1;
  position: relative;
}
footer {
  text-align: center;
  padding: 10px 0;
  background-color: hsl(180, 10, 8);
  color: hsl(0, 0%, 100%);
  text-transform: uppercase;
}
